.app-wrapper {
    margin: 0 auto;
    display: grid;
    width: 1200px;
    grid-template-areas:
    "header header"
    "navbar content";
    grid-template-rows: 100px 1fr;
    grid-template-columns: 100px 10fr;
    /* grid-gap: 10px; */
}

.app-wrapper-content {
    grid-area: content;
    background-color: #0096ff;
}
